
body {
font-family: "Yeon Sung", serif;

}
#imgBg {
  padding: 40px;
  background: url("1215.jpg") top;
  width: 100%;
  
}

.fa {
  display: none;
}

nav ul {
  list-style: none;
  cursor: pointer;
}
nav li {
  display: inline;
  color: white;
  padding: 5px;
  cursor: pointer;
  font-weight: bold;
}
nav li:hover {
  color: azure;

}
nav button {
  display: none;

}

@media (max-width: 585px) {
  nav ul {
    width: 100%;
  text-align: left;
  padding: 0;
  display: none;
  }
nav li {
  display: block;
  color: white;
  
  cursor: pointer;

}
nav button {
  display: inline;
  float: right;
  border: 1px solid #17a2b8;
  border-radius: 3px;
}
nav #lgt {
  width: 100%;

}

nav li:hover {
  background: #17a2b8;
  color: azure;
}

.seen {
  display: block;


}

.fa {
  display: inline;
  float: right;
}
}

#nv {
  background-color: #bfd8d2;
}

.peach {
  color: #fedcd2;
}
.green {
  color: #bfd8d2;
}
.bgpeach {
  background-color: #fedcd2;
font-weight: bold;
}
.bggreen {
  background-color: #bfd8d2;
  font-weight: bold;
}
